// src/pages/interview.js
import { useState, useEffect } from "react";
import { questions } from "@/data/questions";
import Header from "@/layouts/header";
import toast, { Toaster } from "react-hot-toast";
import confetti from "canvas-confetti";

export default function InterviewPage() {
    const [step, setStep] = useState(1);
    const [formData, setFormData] = useState({
        fullName: "",
        email: "",
        phone: "",
        linkedin: "",
        answers: {},
        resume: null,
        coverLetter: null,
    });
    const [currentPage, setCurrentPage] = useState(0);
    const [submissionStatus, setSubmissionStatus] = useState(null);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [isDraggingResume, setIsDraggingResume] = useState(false);
    const [isDraggingCoverLetter, setIsDraggingCoverLetter] = useState(false);
    const [uploadProgress, setUploadProgress] = useState({ resume: 0, coverLetter: 0 });

    const questionsPerPage = 5;
    const totalPages = Math.ceil(questions.length / questionsPerPage);
    const currentQuestions = questions.slice(
        currentPage * questionsPerPage,
        (currentPage + 1) * questionsPerPage
    );

    const handleNext = async () => {
        if (step === 1) {
            if (!formData.fullName || !formData.email) {
                toast.error("Please fill in your name and email.");
                return;
            }
            setStep(2);
            toast.success("Great! Let’s move to the questions.");
        } else if (step === 2) {
            if (!isPageComplete()) {
                toast.error("Please answer all questions on this page.");
                return;
            }
            if (currentPage < totalPages - 1) {
                setCurrentPage(currentPage + 1);
                toast("Moving to the next set of questions...", { icon: "➡️" });
            } else {
                setStep(3);
                toast.success("All questions completed! Upload your documents next.");
            }
        } else if (step === 3) {
            setIsSubmitting(true);
            const maxFileSize = 5 * 1024 * 1024; // 5MB
            if (formData.resume && formData.resume.size > maxFileSize) {
                toast.error("Resume file size exceeds 5MB limit.");
                setIsSubmitting(false);
                return;
            }
            if (formData.coverLetter && formData.coverLetter.size > maxFileSize) {
                toast.error("Cover letter file size exceeds 5MB limit.");
                setIsSubmitting(false);
                return;
            }

            const dataToSend = {
                fullName: formData.fullName,
                email: formData.email,
                phone: formData.phone,
                linkedin: formData.linkedin,
                answers: formData.answers,
                resume: formData.resume ? await fileToBase64(formData.resume) : null,
                coverLetter: formData.coverLetter ? await fileToBase64(formData.coverLetter) : null,
            };

            console.log("Data to send:", {
                ...dataToSend,
                resume: dataToSend.resume ? "present" : "none",
                coverLetter: dataToSend.coverLetter ? "present" : "none",
            });

            const submitToast = toast.loading("Submitting your application...");

            // Simulate upload progress
            const simulateProgress = () => {
                let progress = 0;
                const interval = setInterval(() => {
                    progress += 10;
                    setUploadProgress({
                        resume: formData.resume ? Math.min(progress, 100) : 0,
                        coverLetter: formData.coverLetter ? Math.min(progress, 100) : 0,
                    });
                    if (progress >= 100) clearInterval(interval);
                }, 200);
                return interval;
            };

            const progressInterval = simulateProgress();

            try {
                const response = await fetch("/api/submit", {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json",
                    },
                    body: JSON.stringify(dataToSend),
                });
                const result = await response.json();
                if (response.ok) {
                    clearInterval(progressInterval);
                    setUploadProgress({ resume: 100, coverLetter: 100 });
                    toast.success("Submission successful!", { id: submitToast });
                    setSubmissionStatus({ success: true, score: result.score });
                    setStep(4);
                } else {
                    throw new Error(result.error || "Unknown error");
                }
            } catch (error) {
                clearInterval(progressInterval);
                toast.error(`Submission failed: ${error.message}`, { id: submitToast });
                setSubmissionStatus({ success: false, message: error.message });
            } finally {
                setIsSubmitting(false);
            }
        }
    };

    const handleBack = () => {
        if (step === 2 && currentPage > 0) {
            setCurrentPage(currentPage - 1);
            toast("Going back to the previous set...", { icon: "⬅️" });
        } else if (step > 1) {
            setStep(step - 1);
            toast("Returning to the previous step...", { icon: "⬅️" });
        }
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        if (step === 1) {
            setFormData({ ...formData, [name]: value });
        }
    };

    const handleFileChange = (e, type) => {
        const file = e.target.files[0];
        if (file) {
            setFormData({ ...formData, [type]: file });
            toast.success(`${type === "resume" ? "Resume" : "Cover Letter"} selected: ${file.name}`);
        }
    };

    const handleDrop = (e, type) => {
        e.preventDefault();
        const file = e.dataTransfer.files[0];
        if (file) {
            setFormData({ ...formData, [type]: file });
            toast.success(`${type === "resume" ? "Resume" : "Cover Letter"} dropped: ${file.name}`);
        }
        if (type === "resume") setIsDraggingResume(false);
        else setIsDraggingCoverLetter(false);
    };

    const handleDragOver = (e, type) => {
        e.preventDefault();
        if (type === "resume") setIsDraggingResume(true);
        else setIsDraggingCoverLetter(true);
    };

    const handleDragLeave = (e, type) => {
        e.preventDefault();
        if (type === "resume") setIsDraggingResume(false);
        else setIsDraggingCoverLetter(false);
    };

    const removeFile = (type) => {
        setFormData({ ...formData, [type]: null });
        setUploadProgress({ ...uploadProgress, [type]: 0 });
        toast.success(`${type === "resume" ? "Resume" : "Cover Letter"} removed`);
    };

    const handleOptionToggle = (questionIndex, option) => {
        const currentAnswers = formData.answers[questionIndex] || [];
        let newAnswers;
        if (currentAnswers.includes(option)) {
            newAnswers = currentAnswers.filter((ans) => ans !== option);
        } else {
            newAnswers = questionIndex === 0 ? [...currentAnswers, option] : [option];
        }
        setFormData({
            ...formData,
            answers: { ...formData.answers, [questionIndex]: newAnswers },
        });
        toast(`${option} ${newAnswers.includes(option) ? "selected" : "deselected"}`, {
            duration: 1000,
        });
    };

    const borderColors = [
        "border-red-500",
        "border-blue-500",
        "border-green-500",
        "border-yellow-500",
        "border-purple-500",
    ];

    const isPageComplete = () =>
        currentQuestions.every((q) => formData.answers[q.id - 1]?.length > 0);

    const fileToBase64 = (file) =>
        new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => resolve(reader.result.split(",")[1]);
            reader.onerror = (error) => reject(error);
        });

    // Confetti effect for Step 4
    useEffect(() => {
        if (step === 4 && submissionStatus?.success) {
            confetti({
                particleCount: 100,
                spread: 70,
                origin: { y: 0.6 },
                colors: ["#f05d23", "#231812", "#d94f1e"],
            });
        }
    }, [step, submissionStatus]);

    return (
        <>
            <Header />
            <div className="min-h-screen bg-gray-100 flex flex-col justify-center items-center">
                <div className="max-w-3xl w-full mx-auto p-6">
                    <Toaster position="top-right" reverseOrder={false} />

                    {/* Step 1: Initial Form */}
                    {step === 1 && (
                        <div>
                            <h2 className="text-2xl font-bold text-[#231812] mb-4 text-center">
                                Let’s Get Started
                            </h2>
                            <div className="space-y-4">
                                <input
                                    type="text"
                                    name="fullName"
                                    value={formData.fullName}
                                    onChange={handleChange}
                                    placeholder="Full Name"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="email"
                                    name="email"
                                    value={formData.email}
                                    onChange={handleChange}
                                    placeholder="Email"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="tel"
                                    name="phone"
                                    value={formData.phone}
                                    onChange={handleChange}
                                    placeholder="Phone"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="text"
                                    name="linkedin"
                                    value={formData.linkedin}
                                    onChange={handleChange}
                                    placeholder="LinkedIn Profile (e.g., https://linkedin.com/in/...)"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                            </div>
                        </div>
                    )}

                    {/* Step 2: Questionnaire */}
                    {step === 2 && (
                        <div>
                            <div className="flex justify-between items-center mb-1">
                                <p className="text-xs text-[#231812] font-semibold">
                                    {Math.round(((currentPage + 1) / totalPages) * 100)}%
                                </p>
                                <p className="text-xs text-[#231812] font-semibold">
                                    Page {currentPage + 1} of {totalPages}
                                </p>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2 mb-6">
                                <div
                                    className="bg-[#f05d23] h-2 rounded-full transition-all duration-300"
                                    style={{ width: `${((currentPage + 1) / totalPages) * 100}%` }}
                                ></div>
                            </div>
                            <div className="space-y-6">
                                {currentQuestions.map((question) => (
                                    <div
                                        key={question.id}
                                        className="bg-white shadow-md rounded-lg p-4 transform transition-all hover:scale-105"
                                    >
                                        <p className="text-center text-lg font-bold text-[#231812] mb-5">
                                            {question.text}
                                        </p>
                                        <div className="grid grid-cols-2 md:grid-cols-5 gap-4 place-items-center">
                                            {question.options.map((option, index) => {
                                                const isSelected = (
                                                    formData.answers[question.id - 1] || []
                                                ).includes(option);
                                                return (
                                                    <div
                                                        key={option}
                                                        className="flex flex-col items-center cursor-pointer"
                                                        onClick={() =>
                                                            handleOptionToggle(question.id - 1, option)
                                                        }
                                                    >
                                                        <div
                                                            className={`w-10 h-10 rounded-full flex items-center justify-center border-2 ${
                                                                borderColors[index % borderColors.length]
                                                            } ${
                                                                isSelected
                                                                    ? "bg-[#f05d23] border-[#f05d23]"
                                                                    : "bg-white"
                                                            } transition-all duration-200`}
                                                        >
                                                            {isSelected && (
                                                                <svg
                                                                    viewBox="0 0 24 24"
                                                                    className="w-6 h-6"
                                                                    fill="#ffffff"
                                                                >
                                                                    <path d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
                                                                </svg>
                                                            )}
                                                        </div>
                                                        <p className="text-xs font-semibold text-center mt-2 text-[#231812]">
                                                            {option}
                                                        </p>
                                                    </div>
                                                );
                                            })}
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    )}

                    {/* Step 3: Submit Your Documents */}
                    {step === 3 && (
                        <div className="animate-fade-in">
                            <h2 className="text-2xl font-bold text-[#231812] mb-6 text-center">
                                Submit Your Documents
                            </h2>
                            <div className="space-y-6">
                                {/* Resume Upload */}
                                <div
                                    onDrop={(e) => handleDrop(e, "resume")}
                                    onDragOver={(e) => handleDragOver(e, "resume")}
                                    onDragLeave={(e) => handleDragLeave(e, "resume")}
                                    className={`p-6 flex justify-center bg-white border-2 border-dashed rounded-xl transition-all duration-300 ${
                                        isDraggingResume ? "border-[#f05d23] bg-orange-50" : "border-gray-300"
                                    }`}
                                >
                                    {!formData.resume ? (
                                        <div className="text-center">
                                            <span className="inline-flex justify-center items-center w-10 h-10 bg-gray-100 text-gray-800 rounded-full">
                                                <svg
                                                    className="w-6 h-6"
                                                    xmlns="http://www.w3.org/2000/svg"
                                                    fill="none"
                                                    viewBox="0 0 24 24"
                                                    stroke="currentColor"
                                                    strokeWidth="2"
                                                >
                                                    <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
                                                    <polyline points="17 8 12 3 7 8" />
                                                    <line x1="12" x2="12" y1="3" y2="15" />
                                                </svg>
                                            </span>
                                            <div className="mt-4 text-sm text-gray-600">
                                                <span className="font-medium text-gray-800">Drop your resume here or </span>
                                                <input
                                                    type="file"
                                                    name="resume"
                                                    accept=".pdf,.docx"
                                                    onChange={(e) => handleFileChange(e, "resume")}
                                                    className="hidden"
                                                    id="resume-upload"
                                                />
                                                <label
                                                    htmlFor="resume-upload"
                                                    className="font-semibold text-[#f05d23] hover:text-[#d94f1e] cursor-pointer transition"
                                                >
                                                    browse
                                                </label>
                                            </div>
                                            <p className="mt-1 text-xs text-gray-400">PDF or DOCX, max 5MB</p>
                                        </div>
                                    ) : (
                                        <div className="w-full p-3 bg-white border border-gray-300 rounded-xl">
                                            <div className="mb-1 flex justify-between items-center">
                                                <div className="flex items-center gap-x-3">
                                                    <span className="w-10 h-10 flex justify-center items-center border border-gray-200 text-gray-500 rounded-lg">
                                                        {formData.resume.name.endsWith(".pdf") ? (
                                                            <svg
                                                                className="w-5 h-5"
                                                                xmlns="http://www.w3.org/2000/svg"
                                                                fill="none"
                                                                viewBox="0 0 24 24"
                                                                stroke="currentColor"
                                                            >
                                                                <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8l-6-6z" />
                                                                <path d="M14 2v6h6" />
                                                            </svg>
                                                        ) : (
                                                            <svg
                                                                className="w-5 h-5"
                                                                xmlns="http://www.w3.org/2000/svg"
                                                                fill="none"
                                                                viewBox="0 0 24 24"
                                                                stroke="currentColor"
                                                            >
                                                                <path d="M7 21h10a2 2 0 0 0 2-2V5a2 2 0 0 0-2-2H7a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2z" />
                                                            </svg>
                                                        )}
                                                    </span>
                                                    <div>
                                                        <p className="text-sm font-medium text-gray-800 truncate max-w-[200px]">
                                                            {formData.resume.name}
                                                        </p>
                                                        <p className="text-xs text-gray-500">
                                                            {(formData.resume.size / 1024).toFixed(2)} KB
                                                        </p>
                                                    </div>
                                                </div>
                                                <button
                                                    onClick={() => removeFile("resume")}
                                                    className="text-gray-500 hover:text-gray-800 transition"
                                                >
                                                    <svg
                                                        className="w-4 h-4"
                                                        xmlns="http://www.w3.org/2000/svg"
                                                        fill="none"
                                                        viewBox="0 0 24 24"
                                                        stroke="currentColor"
                                                        strokeWidth="2"
                                                    >
                                                        <path d="M3 6h18" />
                                                        <path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6" />
                                                        <path d="M8 6V4c0-1 1-2 2-2h4c1 0 2 1 2 2v2" />
                                                        <line x1="10" x2="10" y1="11" y2="17" />
                                                        <line x1="14" x2="14" y1="11" y2="17" />
                                                    </svg>
                                                </button>
                                            </div>
                                            {isSubmitting && (
                                                <div className="flex items-center gap-x-3">
                                                    <div className="w-full h-2 bg-gray-200 rounded-full overflow-hidden">
                                                        <div
                                                            className="h-full bg-[#f05d23] rounded-full transition-all duration-500"
                                                            style={{ width: `${uploadProgress.resume}%` }}
                                                        />
                                                    </div>
                                                    <div className="w-10 text-end">
                                                        <span className="text-sm text-gray-800">{uploadProgress.resume}%</span>
                                                    </div>
                                                </div>
                                            )}
                                        </div>
                                    )}
                                </div>

                                {/* Cover Letter Upload */}
                                <div
                                    onDrop={(e) => handleDrop(e, "coverLetter")}
                                    onDragOver={(e) => handleDragOver(e, "coverLetter")}
                                    onDragLeave={(e) => handleDragLeave(e, "coverLetter")}
                                    className={`p-6 flex justify-center bg-white border-2 border-dashed rounded-xl transition-all duration-300 ${
                                        isDraggingCoverLetter ? "border-[#f05d23] bg-orange-50" : "border-gray-300"
                                    }`}
                                >
                                    {!formData.coverLetter ? (
                                        <div className="text-center">
                                            <span className="inline-flex justify-center items-center w-10 h-10 bg-gray-100 text-gray-800 rounded-full">
                                                <svg
                                                    className="w-6 h-6"
                                                    xmlns="http://www.w3.org/2000/svg"
                                                    fill="none"
                                                    viewBox="0 0 24 24"
                                                    stroke="currentColor"
                                                    strokeWidth="2"
                                                >
                                                    <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
                                                    <polyline points="17 8 12 3 7 8" />
                                                    <line x1="12" x2="12" y1="3" y2="15" />
                                                </svg>
                                            </span>
                                            <div className="mt-4 text-sm text-gray-600">
                                                <span className="font-medium text-gray-800">Drop your cover letter here or </span>
                                                <input
                                                    type="file"
                                                    name="coverLetter"
                                                    accept=".pdf,.docx"
                                                    onChange={(e) => handleFileChange(e, "coverLetter")}
                                                    className="hidden"
                                                    id="cover-letter-upload"
                                                />
                                                <label
                                                    htmlFor="cover-letter-upload"
                                                    className="font-semibold text-[#f05d23] hover:text-[#d94f1e] cursor-pointer transition"
                                                >
                                                    browse
                                                </label>
                                            </div>
                                            <p className="mt-1 text-xs text-gray-400">PDF or DOCX, max 5MB</p>
                                        </div>
                                    ) : (
                                        <div className="w-full p-3 bg-white border border-gray-300 rounded-xl">
                                            <div className="mb-1 flex justify-between items-center">
                                                <div className="flex items-center gap-x-3">
                                                    <span className="w-10 h-10 flex justify-center items-center border border-gray-200 text-gray-500 rounded-lg">
                                                        {formData.coverLetter.name.endsWith(".pdf") ? (
                                                            <svg
                                                                className="w-5 h-5"
                                                                xmlns="http://www.w3.org/2000/svg"
                                                                fill="none"
                                                                viewBox="0 0 24 24"
                                                                stroke="currentColor"
                                                            >
                                                                <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8l-6-6z" />
                                                                <path d="M14 2v6h6" />
                                                            </svg>
                                                        ) : (
                                                            <svg
                                                                className="w-5 h-5"
                                                                xmlns="http://www.w3.org/2000/svg"
                                                                fill="none"
                                                                viewBox="0 0 24 24"
                                                                stroke="currentColor"
                                                            >
                                                                <path d="M7 21h10a2 2 0 0 0 2-2V5a2 2 0 0 0-2-2H7a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2z" />
                                                            </svg>
                                                        )}
                                                    </span>
                                                    <div>
                                                        <p className="text-sm font-medium text-gray-800 truncate max-w-[200px]">
                                                            {formData.coverLetter.name}
                                                        </p>
                                                        <p className="text-xs text-gray-500">
                                                            {(formData.coverLetter.size / 1024).toFixed(2)} KB
                                                        </p>
                                                    </div>
                                                </div>
                                                <button
                                                    onClick={() => removeFile("coverLetter")}
                                                    className="text-gray-500 hover:text-gray-800 transition"
                                                >
                                                    <svg
                                                        className="w-4 h-4"
                                                        xmlns="http://www.w3.org/2000/svg"
                                                        fill="none"
                                                        viewBox="0 0 24 24"
                                                        stroke="currentColor"
                                                        strokeWidth="2"
                                                    >
                                                        <path d="M3 6h18" />
                                                        <path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6" />
                                                        <path d="M8 6V4c0-1 1-2 2-2h4c1 0 2 1 2 2v2" />
                                                        <line x1="10" x2="10" y1="11" y2="17" />
                                                        <line x1="14" x2="14" y1="11" y2="17" />
                                                    </svg>
                                                </button>
                                            </div>
                                            {isSubmitting && (
                                                <div className="flex items-center gap-x-3">
                                                    <div className="w-full h-2 bg-gray-200 rounded-full overflow-hidden">
                                                        <div
                                                            className="h-full bg-[#f05d23] rounded-full transition-all duration-500"
                                                            style={{ width: `${uploadProgress.coverLetter}%` }}
                                                        />
                                                    </div>
                                                    <div className="w-10 text-end">
                                                        <span className="text-sm text-gray-800">{uploadProgress.coverLetter}%</span>
                                                    </div>
                                                </div>
                                            )}
                                        </div>
                                    )}
                                </div>
                            </div>
                        </div>
                    )}

                    {/* Step 4: Confirmation */}
                    {step === 4 && (
                        <div className="text-center animate-fade-in-up">
                            <svg
                                className="w-16 h-16 mx-auto mb-4 text-[#f05d23] animate-bounce"
                                fill="none"
                                stroke="currentColor"
                                viewBox="0 0 24 24"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <path
                                    strokeLinecap="round"
                                    strokeLinejoin="round"
                                    strokeWidth="2"
                                    d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
                                />
                            </svg>
                            <h2 className="text-3xl font-bold text-[#231812] mb-4">
                                Thank You, {formData.fullName}!
                            </h2>
                            <p className="text-lg text-[#231812] mb-4">
                                Your application has been successfully submitted.
                            </p>
                            <p className="text-xl font-semibold text-[#f05d23] mb-6">
                                Your Score: {submissionStatus.score}/190
                            </p>
                            <p className="text-sm text-[#231812] max-w-md mx-auto">
                                We’ve sent a confirmation email to {formData.email}. Please check your inbox (and spam folder) for further details.
                            </p>
                        </div>
                    )}

                    {/* Navigation */}
                    {step !== 4 && (
                        <div className="mt-6 flex justify-between">
                            <button
                                onClick={handleBack}
                                disabled={step === 1 || isSubmitting}
                                className="px-4 py-2 bg-gray-400 text-white rounded disabled:opacity-50 hover:bg-gray-500 transition"
                            >
                                Back
                            </button>
                            <button
                                onClick={handleNext}
                                disabled={(step === 2 && !isPageComplete()) || isSubmitting}
                                className="w-full md:w-auto px-4 py-2 bg-[#f05d23] text-white rounded hover:bg-[#d94f1e] disabled:bg-gray-300 disabled:text-gray-600 transition"
                            >
                                {step === 3 ? "Submit" : "Next"}
                            </button>
                        </div>
                    )}
                </div>
            </div>
        </>
    );
}